.header {
  position: fixed;
  z-index: 10;
  min-width: 100%;
  max-width: 100%;
  height: 65px;
  background: #fff;
  -webkit-box-shadow: 0 2px 8px #f0f1f2;
  box-shadow: 0 2px 8px #f0f1f2;
}

.headerLogo {
  width: 120px;
  margin: 15px;
}

.container {
  margin-top: 75px;
  height: 83%;
}

.actionButton {
  float: right;
  margin-top: 20px;
  margin-right: 20px;
}

.actionButton .items:hover {
  cursor: pointer;
  color: #40a9ff;
}

.companyName {
  margin-top: 20px;
}

.authLogo {
  width: 80%;
  margin-bottom: 10%;
}

#root {
  height: 100%;
}

.height100 {
  height: 100%;
}

#components-form-normal-login .login-form {
  max-width: 300px;
}
#components-form-normal-login .login-form-forgot {
  float: right;
}
#components-form-normal-login .login-form-button {
  width: 100%;
}

.ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
  border-right: none;
}

.side-menu{
  border-right: 1px solid #e8e8e8;
  height: 100%;
}

.ant-modal{
  width: 660px !important;
}